import React,{Component} from 'react'
import { Carousel,NoticeBar,List,Checkbox   } from 'antd-mobile'
import './index.css'
import {withRouter} from 'react-router-dom'
const Item = List.Item;
const CheckboxItem = Checkbox.CheckboxItem;
class Home extends Component{
  constructor(props){
    super(props)
    this.state = {
      shoppingList: [
        {id:0, label: '' ,checked:true},
        {id:1, label: '',checked:true },
        {id:2, label: '',checked:true },
      ],
      data: ['1', '2', '3'],
      imgHeight: 176,
      slideIndex: 0
    }
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['16160674/thumb_raw_1515291682.jpeg', '16161362/thumb_raw_1515296260.png' ],
      });
    }, 100);
  }
  onChange = (val) => {
    console.log(val);
  }
  render(){
    return(
      <div>
        {/*  */}
        <NoticeBar mode="closable" action={<span style={{ color: '#a1a1a1' }}></span>}>
          
        </NoticeBar>
        {/*  */}
        <Carousel
          autoplay={false}
          infinite
          selectedIndex={1}
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="javascript(void);"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`https://modao.cc/uploads3/images/1616/${val}`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
        {/*  */}
        <List renderHeader={() => ''} className="my-list">
          <Item onClick={()=>this.props.history.push('/news/ttt')} key='1' extra={'2018/1/12'}></Item>
          <Item key='2' extra={'2018/1/12'}></Item>
          <Item key='3' extra={'2018/1/12'}></Item>
        </List>
        {/* Shopping List */}
        <List  renderHeader={() => {}}>
          {this.state.shoppingList.map((v,i)=> (
            <CheckboxItem key={i} onChange={() => this.onChange(v.label)} checked={v.checked}>
              {v.label}
            </CheckboxItem>
          ))}
        </List>
      </div>
    )
  }
}

export default withRouter(Home)


