import React,{Component} from 'react'
import {SearchBar} from 'antd-mobile'
import ZCDCAccordion from '../ZCDCAccordion'

class Market extends Component {
  constructor(props){
    super(props)
    this.state=({
      recommend:{},
      markets: [{
        title:'',
        info: [{
          name:'',
          price:10,
          place:'88'
        },{
          name:'',
          price:3,
          place:'88'
        }]
      },{
        title:'',
        info: [{
          name:'',
          price:10,
          place:'88'
        },{
          name:'',
          price:3,
          place:'88'
        }]
      }]
    })
  }
  
  render(){
    const getRecommend = (word)=> {
      // console.log(word)
    }
    return(
      <div>
        <SearchBar placeholder="" ref={ref => this.autoFocusInst = ref} onChange={getRecommend}/>
        <ZCDCAccordion details={this.state.markets} />
      </div>
      
    )
  }
}

export default Market;


